@import "../../constants/responsive.scss";
@import "../../constants/fonts.scss";
.report-container {
    background-color: gray;
}
.header {
    margin-bottom: 45px;
    padding: 0px 30px;
    .dateTime {
        display: flex;
        .date {
            flex: 1;
            max-width: 150px;
            span {
                color: #666666;
                font-family: $GilroyMedium;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 16px;
                color: #666666;
            }
        }
        .hour {
            flex: 1;
            span {
                color: #666666;
                font-family: $GilroyMedium;
                font-style: normal;
                font-weight: normal;
                font-size: 14px;
                line-height: 16px;
                color: #666666;
            }
        }
    }
    .report-info {
        flex: 1;
        text-align: end;
        span {
            height: 19px;
            color: #666666;
            font-family: $GilroyMedium;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            color: #666666;
        }
    }
    .report-no {
        display: flex;
        color: #e9686a;
        font-family: $GilroyBlack;
        font-style: normal;
        font-weight: bold;
        font-size: 36px;
        line-height: 44px;
        text-align: end;
        align-items: center;
        justify-content: end;
        span {
            font-family: $GilroyMedium;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            color: #12293f;
            margin-right: 10px;
        }
    }
}
.header-bottom-layer {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding: 0px 30px;
    margin-bottom: 15px;
    .header-bottom-layer-flex-1 {
        flex: 1;
        display: flex;
        align-items: center;
        .flex-vertical {
            flex-direction: column;
        }
        .report-title {
            font-family: $GilroyBlack;
            color: #00856c;
            font-style: normal;
            font-weight: bold;
            font-size: 24px;
        }
        .report-title-description {
            font-family: $GilroyThin;
            color: #999999;
            font-style: normal;
            font-weight: 600;
            font-size: 13px;
            line-height: 20px;
        }
        .report-analysis {
            font-family: $GilroyThin;
            color: #333333;
            font-style: normal;
            font-weight: 200 !important;
            font-size: 24px;
            align-items: center;
            justify-content: center;
            span {
                font-family: $GilroyBlack;
                font-weight: bold;
            }
        }
    }
    .flex-end {
        justify-content: flex-end;
    }
}
.web-report {
    padding: 40px 20px;
    min-height: 100vh;
    background-color: white;
    font-family: $GilroyBlack;
    .func-all {
        margin: 10px 0px;
    }
    .func {
        min-height: 130px;
        @media (max-width: $medium) {
            min-height: 150px;
        }
        border-radius: 15px;
        padding: 10px 20px;
        width: calc(100% - 10px);
        margin: 0px 5px;
    }
    .positive {
        background-color: rgba($color: #04d684, $alpha: 0.12);
    }
    .negative {
        background-color: rgba($color: #e9686a, $alpha: 0.12);
    }
    .functionality-report-element-top {
        width: 100%;
        min-height: 95px;
        align-items: center;
        .flexed-top {
            display: flex;
            flex-direction: row;
            @media (max-width: $medium) {
                flex-direction: column;
            }

            .title {
                flex: 1;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                .titleText {
                    flex: 1;
                }
                @media (max-width: $medium) {
                    text-align: center;
                }
            }
            .boxes {
                max-width: 250px;
                width: auto;
                flex: 1;
                flex-direction: row;
                @media (max-width: $medium) {
                    margin-top: 10px;
                    max-width: 100%;
                }
                .box {
                    flex: 1;
                    max-width: 100px;
                    justify-content: center;
                    align-items: center;
                    @media (max-width: $medium) {
                        max-width: 90px;
                    }
                    img {
                        margin-left: 10px;
                    }
                }
            }
        }
        .functionality-report-element-note {
            height: 40px;
            margin-top: 5px;
            .note-title {
                max-width: 57px;
                flex: 1;
                height: 57px;
                align-items: center;
                justify-content: center;
                color: #666666;
                display: flex;
                margin-left: -7.5px;
                .titleText {
                    flex: 1;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
            .note {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 57px;
                padding: 0px 5px 0px 15px;
                .noteText {
                    flex: 1;
                    font-size: 14px;
                    align-items: center;
                    line-height: 19px;
                    color: #333333;
                    display: -webkit-box;
                    max-width: 100%;
                    flex: 1;
                    min-height: 19px;
                    height: max-content;
                    margin: 0 auto;
                    -webkit-line-clamp: 3;
                    /* autoprefixer: off */
                    -webkit-box-orient: vertical;
                    /* autoprefixer: on */
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }
    }
}
